import logo200Image from 'assets/img/logo/logo_200.png';
import PropTypes from 'prop-types';
import React, { useState} from 'react';
import { Button, Form, FormGroup, Input, Label } from 'reactstrap';
import useUser from '../service/hook/user';
import { Login } from '../service/apiLogin';
import { useFormik } from "formik";
import * as Yup from "yup";

function AuthForm(props) {

  const [loading, setloading] = useState(false)
  const { login } = useUser();
  const formik = useFormik({
    initialValues: initialValues(),
    validationSchema: Yup.object(validationSchema()),
    onSubmit: async(formData) =>{
      const rest = await Login(formData);
      if(rest)login(rest)
    }
  })
  function isLogin() {
    return props.authState === STATE_LOGIN;
  }
  function isSignup() {
    return props.authState === STATE_SIGNUP;
  }

  const changeAuthState = authState => event => {
    event.preventDefault();
    props.onChangeAuthState(authState);
  };

  const handleSubmit = async event => {
    event.preventDefault();
  };

  function renderButtonText() {
    const { buttonText } = props;

    if (!buttonText && isLogin) {
      return 'Login';
    }

    if (!buttonText && isSignup) {
      return 'Signup';
    }

    return buttonText;
  }
  const {
    showLogo,
    usernameLabel,
    usernameInputProps,
    passwordLabel,
    passwordInputProps,
    confirmPasswordLabel,
    confirmPasswordInputProps,
    children,
    onLogoClick,
  } = props;

  return (
    <Form onSubmit={handleSubmit}>
      {showLogo && (
        <div className="text-center pb-4">
          <img
            src={logo200Image}
            className="rounded"
            style={{ width: 60, height: 60, cursor: 'pointer' }}
            alt="logo"
            onClick={onLogoClick}
          />
        </div>
      )}
      <FormGroup>
        <Label for={usernameLabel}>{usernameLabel}</Label>
        <Input
          {...usernameInputProps}
          name="usuario"
          onChange={e => formik.setFieldValue('email', e.target.value)}
          value={formik.values.email}
        />
        <h6>
          {
          formik.errors.email ? <span style={{color:"red"}}>falta llevar el campo email</span> : ""
          }
        </h6>
      </FormGroup>
      <FormGroup>
        <Label for={passwordLabel}>{passwordLabel}</Label>
        <Input
          {...passwordInputProps}
          name="password"
          onChange={e => formik.setFieldValue('password', e.target.value)}
          value={formik.values.password}
        />
          <h6>
          {
          formik.errors.password ? <span style={{color:"red"}}> falta llevar el campo password</span> : ""
          }
        </h6>
      </FormGroup>
      {isSignup() && (
        <FormGroup>
          <Label for={confirmPasswordLabel}>{confirmPasswordLabel}</Label>
          <Input {...confirmPasswordInputProps} name="repitapassword" />
        </FormGroup>
      )}
      <FormGroup check>
        <Label check>
          <Input type="checkbox" />{' '}
          {isSignup() ? 'Agree the terms and policy' : 'Remember me'}
        </Label>
      </FormGroup>
      <hr />
      <Button
        size="lg"
        className="bg-gradient-theme-left border-0"
        block
        onClick={formik.handleSubmit}
        loading={loading}
      >
        {renderButtonText()}
      </Button>

      <div className="text-center pt-1">
        <h6>or</h6>
        <h6>
          {isSignup ? (
            <a href="#login" onClick={changeAuthState(STATE_LOGIN)}>
              Login
            </a>
          ) : (
            <a href="#signup" onClick={changeAuthState(STATE_SIGNUP)}>
              Signup
            </a>
          )}
        </h6>
      </div>

      {children}
    </Form>
  );
}

export const STATE_LOGIN = 'LOGIN';
export const STATE_SIGNUP = 'SIGNUP';

AuthForm.propTypes = {
  authState: PropTypes.oneOf([STATE_LOGIN, STATE_SIGNUP]).isRequired,
  showLogo: PropTypes.bool,
  usernameLabel: PropTypes.string,
  usernameInputProps: PropTypes.object,
  passwordLabel: PropTypes.string,
  passwordInputProps: PropTypes.object,
  confirmPasswordLabel: PropTypes.string,
  confirmPasswordInputProps: PropTypes.object,
  onLogoClick: PropTypes.func,
};

AuthForm.defaultProps = {
  authState: 'LOGIN',
  showLogo: true,
  usernameLabel: 'Email',
  usernameInputProps: {
    type: 'email',
    placeholder: 'your@email.com',
  },
  passwordLabel: 'Password',
  passwordInputProps: {
    type: 'password',
    placeholder: 'your password',
  },
  confirmPasswordLabel: 'Confirm Password',
  confirmPasswordInputProps: {
    type: 'password',
    placeholder: 'confirm your password',
  },
  onLogoClick: () => {},
};

function initialValues(){
  return {
    email: "",
    password:"",
  }
}
function validationSchema(){
  return{
    email: Yup.string(true).email(true).required(true),
    password: Yup.string(true).required(true),
  }
}

export default AuthForm;
