import { local_carrito } from './util/constante'
let CarritoProducto = [];
export const AgregarAlCarrito = async (id, nombre, precio) => {
  VerCarritoItn();
  let cantidad = 1;
  let info = {
    id: id,
    cantidad: cantidad,
    precio: precio,
    nombre: nombre,
  };
  const existe = CarritoProducto.some(producto => producto.id === info.id);
  if (existe) {
    //Actualizamos la cantidad
    const productos = CarritoProducto.map(iten => {
      if (iten.id === info.id) {
        iten.cantidad++;
        return iten; //retorna la cantidad actualizada
      } else {
        return iten; // retorna los objetos que no son actualizado
      }
    });
    CarritoProducto = [...productos];
    await localStorage.setItem(`${local_carrito}`, JSON.stringify(CarritoProducto));
  } else {
    // Agregamosn al carrito
    CarritoProducto = [...CarritoProducto, info];
    await localStorage.setItem(`${local_carrito}`, JSON.stringify(CarritoProducto));
  }
};

const VerCarritoItn = async () => {
  let iten = JSON.parse(localStorage.getItem(`${local_carrito}`));
  if (iten !== null) {;
    CarritoProducto = [];
    CarritoProducto = iten;
  }
};

//optener los productos y la cantidad
export const getProductoLocalStora=async()=>{
    let iten = JSON.parse(await localStorage.getItem(`${local_carrito}`));
    if (iten){
        return iten
    }else{
        return null;
    }
}

