import {host, stora_token} from './util/constante'
export const Login = async user => {

    try {
      const url = `${host}/usuario/login`;
      const params ={
          method:"POST",
          headers: {
              'Content-Type':'application/json',
          },
          body: JSON.stringify(user)
      }
      const rest = await fetch(url, params)
      const token = await rest.json()
      console.log(token.token);
      if(token.message){
         alert(token.message)
      }else{
        setToken(token.token)
        return token.token;
      }
    } catch (error) {
        console.log(error);
    }
};
export const setToken = async token =>{
    await localStorage.setItem(`${stora_token}`,JSON.stringify(token))
}
export const getToken = async() =>{
   const token = await localStorage.getItem(`${stora_token}`)
    return JSON.parse(token);
}
export const removerToken = async() =>{
    await localStorage.removeItem(`${stora_token}`)
    return true;
}