// constantes
const dataInicial = {
    array: []
}
const GET_PRODUCTO = 'GET_PRODUCTO';
// reduces
export default function CarritoLocal(state = dataInicial, action){
    switch (action.type) {
        case GET_PRODUCTO:
            return {...state, array: action.payload};
        default:
            return state;
    }
}

// accion
export const getProductosLocalStore=()=>async(dispatch, getState)=>{
    try {
        dispatch({
            type:GET_PRODUCTO,
            payload: Math.floor(Math.random() * (1000 - 10)) + 20
        })
    } catch (error) {
        console.log(error);
    }
}