import React from 'react';
import PropTypes from 'utils/propTypes';

import { Media } from 'reactstrap';

const Notifications = ({ notificationsData }) => {
  return (
    notificationsData &&
    notificationsData.length &&
    notificationsData.map(({ id, nombre, cantidad }) => (
      <Media key={id} className="pb-2" style={{width:'auto'}}>
        <Media body middle className="align-self-center">
         {cantidad} ) 
        </Media>
        <Media right className="align-self-center">
          {nombre}
        </Media>
      </Media>
    ))
  );
};

Notifications.propTypes = {
  notificationsData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.ID,
      imagen: PropTypes.string,
      nombre: PropTypes.node,
      cantidad: PropTypes.date,
    })
  ),
};

Notifications.defaultProps = {
  notificationsData: [],
};

export default Notifications;
