import Avatar from 'components/Avatar';
import { UserCard } from 'components/Card';
import Notifications from 'components/Notifications';
import SearchInput from 'components/SearchInput';
// import { notificationsData } from 'demos/header';
import withBadge from 'hocs/withBadge';
import React, {useState, useEffect} from 'react';
import useUser from '../../service/hook/user';
import { removerToken } from '../../service/apiLogin'
import { getProductoLocalStora } from '../../service/tienda'
import { useSelector} from 'react-redux'
import {
  MdClearAll,
  MdExitToApp,
  MdHelp,
  MdInsertChart,
  MdMessage,
  MdPersonPin,
  MdSettingsApplications,
  MdAddShoppingCart
} from 'react-icons/md';
import {
  Button,
  ListGroup,
  ListGroupItem,
  // NavbarToggler,
  Nav,
  Navbar,
  NavItem,
  NavLink,
  Popover,
  PopoverBody,
} from 'reactstrap';
import bn from 'utils/bemnames';
import { NavLink as NavLinkPago } from 'react-router-dom';

const bem = bn.create('header');

function Header(props){

  const { logout } = useUser();
  const [state, setstate] = useState({
    isOpenNotificationPopover: false,
    isNotificationConfirmed: false,
    isOpenUserCardPopover: false,
  })

  
  const CerrarSesion = async () => {
    const rest = await removerToken();
    if (rest) {
      logout();
    }
  };
  const [cantidad, setcantidad] = useState(null)
  const [notificationsData, setnotificationsData] = useState([]);

  const tienda = useSelector(store => store.carritolocal.array)
  //function auto ejecutable
  useEffect(() => {
    (async()=>{
      const rest = await getProductoLocalStora();
      if (rest) {
        setnotificationsData(rest);
        let n1 = 0;
        rest.map(iten => (n1 += iten.cantidad));
        setcantidad(n1);
      } else {
        setnotificationsData(null);
      }
    })()
  },[tienda])
  const MdNotificationsActiveWithBadge = withBadge({
    size: 'md',
    color: 'primary',
    style: {
      top: -10,
      right: -10,
      display: 'inline-flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
    children: <small>{cantidad}</small>,
  })(MdAddShoppingCart);
  
  const toggleNotificationPopover = () => {
    setstate({
      isOpenNotificationPopover: !state.isOpenNotificationPopover,
    });

    if (!state.isNotificationConfirmed) {
      setstate({ isNotificationConfirmed: true });
    }
  };

  const toggleUserCardPopover = () => {
    setstate({
      isOpenUserCardPopover: !state.isOpenUserCardPopover,
    });
  };

  const handleSidebarControlButton = event => {
    event.preventDefault();
    event.stopPropagation();

    document.querySelector('.cr-sidebar').classList.toggle('cr-sidebar--open');
  };

    const { isNotificationConfirmed } = state;

    return (
      <Navbar light expand className={bem.b('bg-white')}>
        <Nav navbar className="mr-2">
          <Button outline onClick={handleSidebarControlButton}>
            <MdClearAll size={25} />
          </Button>
        </Nav>
        <Nav navbar>
          <SearchInput />
        </Nav>

        <Nav navbar className={bem.e('nav-right')}>
          <NavItem className="d-inline-flex">
            <NavLink id="Popover1" className="position-relative">
              {isNotificationConfirmed ? (
                <MdNotificationsActiveWithBadge
                  size={25}
                  className="text-secondary can-click animated swing infinite"
                  onClick={()=>toggleNotificationPopover()}
                />
              ) : (
                <>
                <MdNotificationsActiveWithBadge
                  size={25}
                  className="text-secondary can-click animated swing infinite"
                  onClick={()=>toggleNotificationPopover()}
                />
                </>
              )}
            </NavLink>
            <Popover
              placement="bottom"
              isOpen={state.isOpenNotificationPopover}
              toggle={toggleNotificationPopover}
              target="Popover1"
            >
              <PopoverBody >
                <Notifications notificationsData={notificationsData} />
                <NavLinkPago 
                  to="/pago"
                  exact={true}
                >
                  Pagar
                </NavLinkPago>
              </PopoverBody>
            </Popover>
          </NavItem>

          <NavItem>
            <NavLink id="Popover2">
              <Avatar
                onClick={()=>toggleUserCardPopover()}
                className="can-click"
              />
            </NavLink>
            <Popover
              placement="bottom-end"
              isOpen={state.isOpenUserCardPopover}
              toggle={toggleUserCardPopover}
              target="Popover2"
              className="p-0 border-0"
              style={{ minWidth: 250 }}
            >
              <PopoverBody className="p-0 border-light">
                <UserCard
                  title="Jane"
                  subtitle="jane@jane.com"
                  text="Last updated 3 mins ago"
                  className="border-light"
                >
                  <ListGroup flush>
                    <ListGroupItem tag="button" action className="border-light">
                      <MdPersonPin /> Profile
                    </ListGroupItem>
                    <ListGroupItem tag="button" action className="border-light">
                      <MdInsertChart /> Stats
                    </ListGroupItem>
                    <ListGroupItem tag="button" action className="border-light">
                      <MdMessage /> Messages
                    </ListGroupItem>
                    <ListGroupItem tag="button" action className="border-light">
                      <MdSettingsApplications /> Settings
                    </ListGroupItem>
                    <ListGroupItem tag="button" action className="border-light">
                      <MdHelp /> Help
                    </ListGroupItem>
                    <ListGroupItem tag="button" onClick={()=>CerrarSesion()} className="border-light">
                      <MdExitToApp /> Signout
                    </ListGroupItem>
                  </ListGroup>
                </UserCard>
              </PopoverBody>
            </Popover>
          </NavItem>
        </Nav>
      </Navbar>
    );
}

export default Header;
